.countdown-block {
	padding: 80px 0;
	margin: 40px 0;
	position: relative;
	background: $yellow-gradient;
	@include mq($max-width: 1200px) {
		padding: 40px 0;
		margin: 40px 0; }
	.circle1 {
		position: absolute;
		left: 6%;
		top: 0;
		@include mq($max-width: 700px) {
			display: none; } }
	.circle2 {
		position: absolute;
		z-index: -1;
		right: 0;
		top: 50%;
		transform: translate(15%, -45%);
		@include mq($max-width: 1200px) {
			right: 50%;
			transform: translate(50%, -50%); }
		@include mq($max-width: 700px) {
			display: none; } }
	.circle3 {
		position: absolute;
		left: 0;
		bottom: -80px;
		@include mq($max-width: 1200px) {
			display: none; } }
	&__arrow {
		position: absolute;
		left: 0;
		bottom: 20%;
		transform: translate(-110%, 0);
		@include mq($max-width: 1200px) {
			display: none; } }
	.block-title {
		margin: 0 0 15px 0;
		@include mq($max-width: 1200px) {
			text-align: center; } }
	.container {
		position: relative; }
	.inner {
		@include flex(row, between, null, wrap);
		position: relative;
		z-index: 2;
		margin: 0 0 35px 0; }
	&-left {
		max-width: 570px;
		width: 100%;
		@include mq($max-width: 1200px) {
			max-width: 100%;
			@include flex(column, null, center, nowrap);
			margin: 0 0 20px 0; } }
	&-right {
		max-width: 520px;
		width: 100%;
		position: relative;
		@include mq($max-width: 1200px) {
			@include flex(row, center, center, auto);
			max-width: 100%; } }
	.prices {
		max-width: 480px;
		width: 100%;
		margin: 0 0 20px 0;
		@include mq($max-width: 400px) {
			text-align: center;
			@include flex(row, between, null, wrap);
			padding: 0 5px; }
		li {
			@include flex(row, null, center, wrap);
			margin: 0 0 6px 0;
			@include mq($max-width: 400px) {
				width: 50%; } }
		&__text {
			margin: 0 auto 0 0;
			@include mq($max-width: 575px) {
				font-size: 14px; }
			@include mq($max-width: 400px) {
				width: 100%; } }
		&__old {
			text-decoration: line-through;
			font-size: 14px;
			@include mq($max-width: 575px) {
				font-size: 12px; }
			@include mq($max-width: 400px) {
				width: 100%;
				margin: 5px 0; } }
		&__curr {
			min-width: 100px;
			font-size: 14px;
			padding: 4px;
			background: white;
			margin: 0 0 0 10px;
			text-align: right;
			@include mq($max-width: 575px) {
				font-size: 12px; }
			@include mq($max-width: 400px) {
				text-align: center;
				margin: 0 auto; }
			b {
				font-size: 18px;
				font-family: 'Exo', sans-serif;
				font-weight: 700;
				@include mq($max-width: 575px) {
					font-size: 15px; } } } }
	.form {
		&-item {
			margin: 0 0 10px 0; }
		&__input {
			margin: 0 15px 0 0;
			@include mq($max-width: 575px) {
				margin: 0 0 10px 0;
				width: 100%;
				text-align: center; } }
		&__btn {
			min-width: 255px;
			@include mq($max-width: 575px) {
				width: 100%; } }
		&__text {
			@include mq($max-width: 575px) {
				width: 100%; } } }
	.list {
		width: 90%;
		@include flex(row, start, start, wrap);
		@include mq($max-width: 1200px) {
			width: 100%;
			justify-content: center;
			text-align: center; }
		&-item {
			width: 50%;
			@include mq($max-width: 1200px) {
				margin: 0 2%;
				max-width: 30%; }
			@include mq($max-width: 800px) {
				max-width: 46%; }
			@include mq($max-width: 575px) {
				width: 48%; }
			&__title {
				font: 800 18px 'Exo', sans-serif;
				@include mq($max-width: 1200px) {
					font-size: 16px; }
				@include mq($max-width: 575px) {
					font-size: 15px; }
				span {
					font-size: 72px;
					@include mq($max-width: 1200px) {
						font-size: 50px; }
					@include mq($max-width: 575px) {
						font-size: 36px; } } }
			&__text {
				margin: 10px 0 0 0;
				font-weight: 500;
				@include mq($max-width: 575px) {
					font-size: 14px; } } } }
	&__img {
		display: block;
		width: 100%;
		height: auto;
		@include mq($max-width: 1200px) {
			width: 50%; }
		@include mq($max-width: 700px) {
			width: 70%; } }
	.action {
		width: 100%;
		text-align: center;
		position: relative;
		z-index: 3; }

	.counter {
		max-width: 370px;
		width: 100%;
		padding: 24px 24px 40px 24px;
		border-radius: 4px;
		background: white;
		position: absolute;
		z-index: 3;
		left: 0;
		bottom: 0;
		@include mq($max-width: 1200px) {
			left: auto;
			right: auto; }
		@include mq($max-width: 575px) {
			max-width: 100%;
			padding: 10px 10px 30px 10px; }
		@include mq($max-width: 400px) {
			padding: 10px; }
		&__title {
			font-size: 16px;
			font-weight: 700;
			line-height: 1.2;
			@include mq($max-width: 575px) {
				font-size: 15px; } }
		&__text {
			margin: 6px 0 10px;
			font-size: 14px;
			@include mq($max-width: 575px) {
				font-size: 13px; } } }
	.flipper {
		width: 100%;
		height: 70px;
		@include flex(row, null, start, nowrap); }
	.flipper-group {
		@include flex(row, center, null, nowrap);
		.flipper-delimiter {
			display: none; } }
	.flipper-delimiter {
		opacity: 0.3;
		color: $dark;
		font-size: 50px;
		line-height: 70px;
		vertical-align: middle;
		padding: 0;
		margin: 0;
		float: left;
		@include mq($max-width: 400px) {
			line-height: 50px; } }
	.flipper-digit {
		height: 70px;
		margin: 0 2px;
		@include mq($max-width: 400px) {
			height: 50px; }
		&::after {
			display: none; } }
	.flipper-group label {
		color: $dark;
		opacity: .7;
		font-size: 14px; }
	.digit-next,
	.digit-top,
	.digit-top2,
	.digit-face,
	.digit-bottom {
		height: 70px;
		color: white;
		background: $dark;
		font-weight: 800;
		letter-spacing: 0.1em;
		font-size: 36px;
		border-radius: 4px;
		@include flex(column, center, center, nowrap);
		line-height: 1;
		padding: 0 .1em;
		@include mq($max-width: 400px) {
			font-size: 26px;
			height: 50px; }
		&.r {
			display: none;
			padding: 0; } } }
